import {
    Flex,
    Box,
    Image,
    Button
} from '@chakra-ui/react';
import { useXPContext } from "../Contexts/XPContext";

interface ChildProps {
    currentCalculator: number;
    setCalculator: React.Dispatch<React.SetStateAction<number>>;
}

const CalculatorSwitcher: React.FC<ChildProps> = ({currentCalculator, setCalculator}) => {
    const {
        setXPMultiplier
    } = useXPContext();

    const setNewCalculator = (value: number) => {
        setCalculator(value);
        setXPMultiplier(1);
    }

    return (
        <Box borderWidth='1px' borderRadius='lg' mb={2} maxWidth="100%">
            <Flex p={2} gap={2} overflowX="scroll">
                <Button 
                    onClick={() => setNewCalculator(1)} 
                    colorScheme="gray"
                    border={currentCalculator === 1 ? "2px" : "0px"}
                    borderColor='green.500'
                    flexShrink={0}
                >
                    <Image src="https://oldschool.runescape.wiki/images/archive/20141020205319%21Prayer_icon.png?efe42"/>
                </Button>
                <Button 
                    onClick={() => setNewCalculator(2)} 
                    colorScheme="gray"
                    border={currentCalculator === 2 ? "2px" : "0px"}
                    borderColor='green.500'
                    flexShrink={0}
                >
                    <Image src="https://oldschool.runescape.wiki/images/Herblore_icon.png?ffa9e"/>
                </Button>
            </Flex>
        </Box>
    ); 
};

export default CalculatorSwitcher;